@font-face {
    font-family: 'Arimo';
    src: url("../fonts/Arimo/arimo-regular-webfont.woff") format("woff"),
    url("../fonts/Arimo/arimo-regular-webfont.woff2") format("woff2");
    font-weight: 300;
    font-style: normal;
}

@font-face {
    font-family: 'Arimo';
    src: url("../fonts/Arimo/arimo-italic-webfont.woff") format("woff"),
    url("../fonts/Arimo/arimo-italic-webfont.woff2") format("woff2");
    font-weight: 300;
    font-style: italic;
}

@font-face {
    font-family: 'Arimo';
    src: url("../fonts/Arimo/arimo-bold-webfont.woff") format("woff"),
    url("../fonts/Arimo/arimo-bold-webfont.woff2") format("woff2");
    font-weight: 600;
    font-style: normal;
}

@font-face {
    font-family: 'Arimo';
    src: url("../fonts/Arimo/arimo-bolditalic-webfont.woff") format("woff"),
    url("../fonts/Arimo/arimo-bolditalic-webfont.woff2") format("woff2");
    font-weight: 600;
    font-style: italic;
}