.products__block {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    .product__tizer {
        flex: 0 0 25%;
    }
}

.product__card {
    margin-top: 50px;
}
.product__pic {
    width: 100%;
    height: auto;
}

.product__title {
    font-size: 24px;
    color: $black;
    text-transform: uppercase;
    margin-bottom: 20px;
}

.product__util {
    color: $grey;
    font-size: 14px;
    font-style: italic;
    margin-right: 30px;
}

.condition {
    color: $green;
}

.product__price {
    color: $black;
    font-size: 36px;
    font-weight: 600;
    margin-bottom: 20px;
}

.buy-form {
    padding-right: 150px;
}
.input__row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    width: 50%;
}

.input__label {
    margin-bottom: 0;
    position: relative;
    cursor: pointer;
    &:before {
        display: block;
        content: '';
        position: absolute;
        width: 15px;
        height: 15px;
        border: 1px solid $grey;
        border-radius: 50%;
        left: -20px;
        top: 50%;
        transform: translateY(-50%);
    }
    &:after {
        display: block;
        content: '';
        position: absolute;
        width: 11px;
        height: 11px;
        border-radius: 50%;
        left: -18px;
        top: 50%;
        transform: translateY(-50%);
    }
}

.input-radio {
    display: none;
}

.input-radio:checked {
    &+.input__label {
        &:after {
            background-color: $black;
        }
    }
}

.form__input {
    width: 50px;
    color: $grey;
    font-size: 14px;
    padding: 10px 15px;
    text-align: center;
    border-radius: 3px;
    border: 1px solid $grey;
}

.product__socials {
    margin-top: 20px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.social__item {
    margin-right: 20px;
    &:last-child {
        margin-left: 5px;
    }
}

.social__link {
    color: $grey;
    font-size: 18px;
    &:hover {
        color: $green;
    }
}

.product__links {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 80%;
    margin-top: 20px;
    position: relative;
}

.product__link {
    color: $black;
    font-size: 16px;
    &:hover {
        color: $green;
    }
}

.link__icon {
    margin-right: 20px;
}

.separator {
    display: block;
    width: 1px;
    height: 20px;
    background-color: $grey;
    margin: 0 20px;
}

.product__info {
    margin-top: 20px;
}

.info__title {
    color: $black;
    font-size: 28px;
    position: relative;
    &:before {
        display: block;
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        background-color: $green;
    }
}

.info__text {
    color: $grey;
}