.footer {
    background-color: #fff;
    border-bottom: 1px solid #e8e8e8;
}

.footer__top {
    padding: 25px 0;
    border-bottom: 1px solid #e8e8e8;
}

.footer__column {
    margin: 60px 0 30px 0;
}

.column__title {
    font-size: 28px;
    color: $black;
}

.column__item {
    margin: 5px 0;
}

.contact__item {
    color: $grey;
    margin: 5px 0;
}

.column__link {
    color: $black;
    &:hover {
        color: $green;
    }
}