.tizers__content {
    padding: 30px 0;
}

.tizers__block {
    position: relative;
    margin-top: 20px;
}

.product__tizer {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: $black;
    font-size: 16px;
    overflow: hidden;
    &:hover {
        color: $black;
        .tizer__sublink {
            bottom: 0;
        }
    }
}

.tizer__sublink {
    position: absolute;
    bottom: -100%;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 60px;
    width: 100%;
    height: 100%;
    transition: .3s;
}

.tizer__pic {
    max-width: 100%;
    height: auto;
}

.tizer__title {
    text-align: center;
}

.tizer__price {
    font-weight: 600;
    font-size: 20px;
}

.slider__btn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 9999;
    color: $black;
    font-size: 20px;
    &:hover {
        color: $green;
    }
}

.prev {
    left: 0;
}

.next {
    right: 0;
}