.header {
    padding: 20px 0;
    padding-bottom: 30px;
    background-color: #fff;
    border-bottom: 1px solid #e8e8e8;
}

.header__block {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    height: 100%;
    padding-bottom: 20px;
}

.header__logo {
    text-align: center;
}

.header__login-link {
    color: $grey;
    font-size: 13px;
    &:hover {
        color: $green;
    }
}

.search__icon {
    color: #000;
    font-size: 28px;
    transition: .3s;
    &:hover {
        color: $green;
    }
}

.header__language {
    color: $grey;
    font-size: 13px;
}

.language__link {
    color: $black;
    &:hover {
        color: $green;
    }
}

.language__icon {
    font-size: 12px;
}

.cart__icon {
    color: #000;
    font-size: 28px;
    transition: .3s;
    &:hover {
        color: $green;
    }
}

.menu__list {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px 0;
}

.menu__item {
    margin: 0 10px;
    &:hover {
        .sub-menu {
            display: block;
        }
    }
}

.sub-menu {
    z-index: 9999999999999;
    width: 1140px;
    display: none;
    position: absolute;
    top: 40px;
    left: 0;
    background-color: #fff;
    padding: 70px 50px;
}

.menu__link {
    position: relative;
    color: $black;
    text-transform: uppercase;
    font-size: 13px;
    &:after {
        position: absolute;
        left: 50%;
        bottom: -5px;
        content: '';
        display: block;
        width: 0;
        height: 2px;
        border-radius: 3px;
        background-color: $green;
    }
    &:hover {
        color: $black;
        &:after {
            animation: links .5s linear both;
        }
    }
}

.menu__wrapper {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
}

.sub-menu__list {
    flex: 0 0 50%;
    text-transform: uppercase;
    margin-bottom: 30px;
    border-bottom: 1px solid #ccc;
    padding-bottom: 50px;
}

.sub-menu__link {
    color: $green;
    &:hover {
        color: $grey;
    }
}