.banners__container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.banners__pic  {
    width: 100%;
    max-height: 490px;
}

.banners__row {
    margin-top: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .banners__item {
        margin-right: 5px;
        &:last-child {
            margin-right: 0;
        }
    }
}