.btn {
  cursor: pointer;
  text-align: center;
  background-color: #a1c133;
  border: none;
  color: #fff;
  width: 100%;
  border-radius: 3px;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 14px;
  padding: 15px 60px;
  overflow: hidden;
  position: relative;
}

.btn:before {
  display: block;
  content: '';
  position: absolute;
  width: 60%;
  height: 100%;
  top: 0;
  left: -100%;
  background-color: rgba(255, 255, 255, 0.3);
  transform: skew(45deg);
  transition: .3s;
}

.btn:after {
  display: block;
  content: '';
  position: absolute;
  width: 60%;
  height: 100%;
  top: 0;
  right: -100%;
  background-color: rgba(255, 255, 255, 0.3);
  transform: skew(45deg);
  transition: .3s;
}

.btn:hover:before {
  left: -10%;
}

.btn:hover:after {
  right: -10%;
}

.btn--tizer {
  padding: 15px;
}

.btn--tizer:before {
  width: 64%;
}

.btn--tizer:after {
  width: 64%;
}

.btn--tizer:hover:before {
  left: -14%;
}

.btn--tizer:hover:after {
  right: -14%;
}

