.clinic__title {
    width: 100%;
    text-align: center;
    color: $grey;
    h1 {
        font-size: 36px;
        text-transform: uppercase;
    }
}

.accordion {
    margin-top: 50px;
    padding: 0 50px;
    color: $grey;
    list-style: disc;
}

.acco__title {
    font-size: 24px;
    font-weight: 600;
    text-transform: uppercase;
}

.acco__text {
    padding: 30px;
}

.research__content {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
}

.research__text {
    padding-left: 10px;
    color: $grey;
}

.research__title {
    display: inline-block;
    margin: 10px 0;
    font-size: 20px;
    font-weight: 600;
}