.cat__banner {
    background-color: #e8e8e8;
    position: relative;
}

.banner__block {
    width: 50%;
    padding: 90px 0;
}

.cat-banner__title {
    font-size: 20px;
    text-transform: uppercase;
    font-weight: 600;
    margin-bottom: 20px;
}

.cat-banner__text {
    color: $grey;
}

.cat-banner__image {
    position: absolute;
    top: 50%;
    right: 250px;
    transform: translateY(-50%);
    max-height: 400px;
}

.cat__filter {
    max-width: 100%;
    height: auto;
}

.cat__filters {
    margin-top: 20px;
}

.cat__products {
    border-top: 1px solid $grey;
    border-bottom: 1px solid $grey;
    padding: 30px 0;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    .product__tizer {
        flex: 0 0 33%;
    }
}